function centerModal() {
    $(this).css('display', 'block');
    var $dialog = $(this).find(".modal-dialog");
    var offset = ($(window).height() - $dialog.height()) / 2;
    $dialog.css("margin-top", offset);
}

function validate(target) {

    /*var name, tel;

    name = $(target).find('input:text').val();
    tel = $(target).find('input:tel').val();*/

    // formValid
    var formValid = true;

    $(target).find('.submit').attr('disabled', 'disabled');

    $(target).find('input.validating').each(function () {
        // ,    .form-group,   success/error
        var formGroup = $(this).parents('.form-group');
        // glyphicon,        
        var glyphicon = formGroup.find('.form-control-feedback');
        //    HTML5  checkValidity

        if (this.checkValidity() && ($(this).val() != "")) {
            //  formGroup  .has-success,  has-error
            formGroup.addClass('has-success').removeClass('has-error');
            //  glyphicon  glyphicon-ok,  glyphicon-remove
            glyphicon.addClass('glyphicon-ok').removeClass('glyphicon-remove');
        } else {
            //  formGroup  .has-error,  .has-success
            formGroup.addClass('has-error').removeClass('has-success');
            //  glyphicon  glyphicon-remove,  glyphicon-ok
            glyphicon.addClass('glyphicon-remove').removeClass('glyphicon-ok');
            //   
            formValid = false;
        }
    });

    //  , 
    if (formValid) {
        return true;
    }

    return false;
}

